___CSS_LOADER_EXPORT___.push([module.id, `input[type="checkbox"] {
  --main-background: #fff;
  
  flex-shrink: 0;

  appearance: none;
  width: 1em;
  height: 1em;
  border: 1px solid var(--secondary-text);
  border-radius: 50%;
  
  display: grid;
  place-content: center;
  
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover, &:checked {
    background-color: var(--main-background);
  }

  &::before {
    content: "";
    width: 0.5em;
    height: 0.5em;
    transform: scale(0);
    box-shadow: inset 1em 1em #22ff00;
  
    transition: transform 0.2s;
  
    /*Checkmark*/
    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  }

  &:checked::before {
    transform: scale(1);
  }
}`, "",{"version":3,"sources":["webpack://./src/pages/project/components/todosList/components/todoCard/components/checkbox/checkbox.css"],"names":[],"mappings":"AAAA;EACE,uBAAuB;;EAEvB,cAAc;;EAEd,gBAAgB;EAChB,UAAU;EACV,WAAW;EACX,uCAAuC;EACvC,kBAAkB;;EAElB,aAAa;EACb,qBAAqB;;EAErB,eAAe;EACf,iCAAiC;;EAEjC;IACE,wCAAwC;EAC1C;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,iCAAiC;;IAEjC,0BAA0B;;IAE1B,YAAY;IACZ,uEAAuE;EACzE;;EAEA;IACE,mBAAmB;EACrB;AACF","sourcesContent":["input[type=\"checkbox\"] {\n  --main-background: #fff;\n  \n  flex-shrink: 0;\n\n  appearance: none;\n  width: 1em;\n  height: 1em;\n  border: 1px solid var(--secondary-text);\n  border-radius: 50%;\n  \n  display: grid;\n  place-content: center;\n  \n  cursor: pointer;\n  transition: background-color 0.2s;\n\n  &:hover, &:checked {\n    background-color: var(--main-background);\n  }\n\n  &::before {\n    content: \"\";\n    width: 0.5em;\n    height: 0.5em;\n    transform: scale(0);\n    box-shadow: inset 1em 1em #22ff00;\n  \n    transition: transform 0.2s;\n  \n    /*Checkmark*/\n    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);\n  }\n\n  &:checked::before {\n    transform: scale(1);\n  }\n}"],"sourceRoot":""}]);
