___CSS_LOADER_EXPORT___.push([module.id, `#todos {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.icon-btn {
  display: grid;
  place-content: center;
  border: none;
  background: none;
  
  cursor: pointer;

  &:hover {
    animation: shake 0.2s;
  }
}

@keyframes shake {
  0% { transform: rotate(0); }
  25% { transform: rotate(-25deg); }
  75% { transform: rotate(25deg); }
  100% { transform: rotate(0); }
}

.icon-btn--rounded {
  border: 1px solid var(--secondary-background);
  border-radius: 50%;
  padding: 8px;
  background-color: var(--secondary-background);

  transition: background-color 0.2s;

  &:hover {
    background-color: var(--main-background);
  }
}`, "",{"version":3,"sources":["webpack://./src/pages/project/project.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,YAAY;EACZ,gBAAgB;;EAEhB,eAAe;;EAEf;IACE,qBAAqB;EACvB;AACF;;AAEA;EACE,KAAK,oBAAoB,EAAE;EAC3B,MAAM,yBAAyB,EAAE;EACjC,MAAM,wBAAwB,EAAE;EAChC,OAAO,oBAAoB,EAAE;AAC/B;;AAEA;EACE,6CAA6C;EAC7C,kBAAkB;EAClB,YAAY;EACZ,6CAA6C;;EAE7C,iCAAiC;;EAEjC;IACE,wCAAwC;EAC1C;AACF","sourcesContent":["#todos {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n\n.icon-btn {\n  display: grid;\n  place-content: center;\n  border: none;\n  background: none;\n  \n  cursor: pointer;\n\n  &:hover {\n    animation: shake 0.2s;\n  }\n}\n\n@keyframes shake {\n  0% { transform: rotate(0); }\n  25% { transform: rotate(-25deg); }\n  75% { transform: rotate(25deg); }\n  100% { transform: rotate(0); }\n}\n\n.icon-btn--rounded {\n  border: 1px solid var(--secondary-background);\n  border-radius: 50%;\n  padding: 8px;\n  background-color: var(--secondary-background);\n\n  transition: background-color 0.2s;\n\n  &:hover {\n    background-color: var(--main-background);\n  }\n}"],"sourceRoot":""}]);
