___CSS_LOADER_EXPORT___.push([module.id, `.priority {
  --label-color: var(--secondary-background);
  
  background-color: var(--label-color);
  color: #fff;
  font-weight: bold;
  font-size: small;
  width: fit-content;
  padding: 4px 8px;
  border: none;
  border-radius: 16px;
  cursor: pointer;

  display: none;
  opacity: 0;
  transition-property: opacity, display, background-color;
  transition-duration: 0.25s;
  transition-behavior: allow-discrete;
  
  &:not(.hidden) {
    opacity: 1;
    display: block;

    @starting-style {
      opacity: 0;
    }

    &:hover {
      opacity: 0.5;
    }
  }
}

.priority--high {
  --label-color: #ff0000;
}

.priority--medium {
  --label-color: #e5ff00;
  color: #000;
}

.priority--low {
  --label-color: #22ff00;
  color: #000;
}`, "",{"version":3,"sources":["webpack://./src/pages/project/components/todosList/components/todoCard/components/priorityLabel/priorityLabel.css"],"names":[],"mappings":"AAAA;EACE,0CAA0C;;EAE1C,oCAAoC;EACpC,WAAW;EACX,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;EAChB,YAAY;EACZ,mBAAmB;EACnB,eAAe;;EAEf,aAAa;EACb,UAAU;EACV,uDAAuD;EACvD,0BAA0B;EAC1B,mCAAmC;;EAEnC;IACE,UAAU;IACV,cAAc;;IAEd;MACE,UAAU;IACZ;;IAEA;MACE,YAAY;IACd;EACF;AACF;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,sBAAsB;EACtB,WAAW;AACb","sourcesContent":[".priority {\n  --label-color: var(--secondary-background);\n  \n  background-color: var(--label-color);\n  color: #fff;\n  font-weight: bold;\n  font-size: small;\n  width: fit-content;\n  padding: 4px 8px;\n  border: none;\n  border-radius: 16px;\n  cursor: pointer;\n\n  display: none;\n  opacity: 0;\n  transition-property: opacity, display, background-color;\n  transition-duration: 0.25s;\n  transition-behavior: allow-discrete;\n  \n  &:not(.hidden) {\n    opacity: 1;\n    display: block;\n\n    @starting-style {\n      opacity: 0;\n    }\n\n    &:hover {\n      opacity: 0.5;\n    }\n  }\n}\n\n.priority--high {\n  --label-color: #ff0000;\n}\n\n.priority--medium {\n  --label-color: #e5ff00;\n  color: #000;\n}\n\n.priority--low {\n  --label-color: #22ff00;\n  color: #000;\n}"],"sourceRoot":""}]);
