___CSS_LOADER_EXPORT___.push([module.id, `.todo-card {
  --main-background: #eee;

  border: 1px solid var(--secondary-background);
  border-radius: 16px;
  padding: 8px;
  width: 200px;

  display: flex;
  flex-direction: column;
  gap: 4px;

  cursor: pointer;

  transition:
    width 1s,
    gap 1s,
    border-width 0.2s,
    border-radius 0.2s,
    background-color 0.2s;

  &:not(.todo-card--completed):hover {
    background-color: var(--main-background);
  }
}

.todo-card--expanded {
  width: 100%;
  border-left: none;
  border-right: none;
  border-radius: 0;
  gap: 8px;

  /*For card contraction to work well,
  all the items inside the expanded card
  don't have to occupy more space than 
  their content*/
  & > * {
    width: fit-content;
  }

  & .dueDate:hover {
    font-weight: bold;
  }
}

.todo-card--completed {
  background-color: var(--secondary-background);

  & .title {
    text-decoration: line-through;
  }
}`, "",{"version":3,"sources":["webpack://./src/pages/project/components/todosList/components/todoCard/todo-card.css"],"names":[],"mappings":"AAAA;EACE,uBAAuB;;EAEvB,6CAA6C;EAC7C,mBAAmB;EACnB,YAAY;EACZ,YAAY;;EAEZ,aAAa;EACb,sBAAsB;EACtB,QAAQ;;EAER,eAAe;;EAEf;;;;;yBAKuB;;EAEvB;IACE,wCAAwC;EAC1C;AACF;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,kBAAkB;EAClB,gBAAgB;EAChB,QAAQ;;EAER;;;gBAGc;EACd;IACE,kBAAkB;EACpB;;EAEA;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE,6CAA6C;;EAE7C;IACE,6BAA6B;EAC/B;AACF","sourcesContent":[".todo-card {\n  --main-background: #eee;\n\n  border: 1px solid var(--secondary-background);\n  border-radius: 16px;\n  padding: 8px;\n  width: 200px;\n\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n\n  cursor: pointer;\n\n  transition:\n    width 1s,\n    gap 1s,\n    border-width 0.2s,\n    border-radius 0.2s,\n    background-color 0.2s;\n\n  &:not(.todo-card--completed):hover {\n    background-color: var(--main-background);\n  }\n}\n\n.todo-card--expanded {\n  width: 100%;\n  border-left: none;\n  border-right: none;\n  border-radius: 0;\n  gap: 8px;\n\n  /*For card contraction to work well,\n  all the items inside the expanded card\n  don't have to occupy more space than \n  their content*/\n  & > * {\n    width: fit-content;\n  }\n\n  & .dueDate:hover {\n    font-weight: bold;\n  }\n}\n\n.todo-card--completed {\n  background-color: var(--secondary-background);\n\n  & .title {\n    text-decoration: line-through;\n  }\n}"],"sourceRoot":""}]);
